<template>
    <div class="container">
        
        <table>
            <tbody>
                <tr>
                    <th :style="{ backgroundImage: `url(${flag})`}"  class="currency" rowspan="2">{{currency[0]}}<span class="current_name">
                        {{currency[1]}}</span></th>
                    <th>Total:</th>
                    <th>Available:</th>
                    <th class="red">Blocked:</th>
                    <th class="black">Bank account №:</th>
                </tr>
                <tr>
                    <td>{{getBalance.total.toFixed(2)}}</td>
                    <td>{{getBalance.available.toFixed(2)}}</td>
                    <td>{{getBalance.blocked.toFixed(2)}}</td>
                    <td class="blue">{{formatAccount}}</td> <!-- split string -->
                </tr> 
               </tbody>  
        </table>
    </div>
</template>

<script>
    export default{
        name: "current-table",
        props: {
            account: Number,
            currency: Array,
            flag: String,
            balance: Object
        },
        methods: {
            
        },
        computed: {
            formatAccount(){
                let account = "0" + this.account.toString()
            
               
                let output = account.substr(0, 3)
                output += " " + account.substr(3, 2)
                output += " " + account.substr(5, 3)
                output += " " + account.substr(8, 4)
               
                return output
            },
            getBalance(){
                let currency = this.currency[1].toLowerCase()
                let balance = {
                    total: this.balance.total[currency],
                    available: this.balance.total[currency] - this.balance.blocked[currency],
                    blocked: this.balance.blocked[currency]
                }
                return balance
            }
        }
    }
</script>
