    <div class="container">
        <div class="wrapper">
            <header>
                <div class="left">
                    <img src="../assets/logo_green.png" alt="" width="120" height="120">
                    <div class="titles" v-if="!loading">
                        <span class="label">Business account</span>
                        <span class="titles__corporate">{{currentUser.company.name}}</span>
                        <span class="titles__id">ID: <span style="color: blue">{{formatId()}}</span></span>
                    </div>
                </div>
                <div class="right">
                    <div class="panel header_button">
                        <button @click="currentModal='profile'">Profile</button>
                        <button @click="currentModal='transactionHistory'">History</button>
                        <button @click="currentModal='info'">Info</button>
                    </div>
                    <div class="user">
                        <img src="../assets/icons/icon_user_2.svg" alt="" width="45" height="45">
                        <span @click="logOut()">Welcome Mr. {{getInfoFromLocal().name}}</span>
                    </div>
                </div>
            </header>
            <section>
                <div class="balance-container" v-if="!loading">
                    <span>Balance:</span>
                    <balance :currency="['$', 'USD']" :flag="flagUrl.usa" :balance="currentUser.balance" :account="currentUser.accounts.usd" @click.native="currentModal='accountRequisites', thisCurrency='usd'"/>
                    <balance :currency="['€', 'EUR']" :flag="flagUrl.eu" :balance="currentUser.balance" :account="currentUser.accounts.eur" @click.native="currentModal='accountRequisites', thisCurrency='eur'"/>
                    <balance :currency="['£', 'GBP']" :flag="flagUrl.gb" :balance="currentUser.balance" :account="currentUser.accounts.gbp" @click.native="currentModal='accountRequisites', thisCurrency='gbp'"/>
                    <balance :currency="['₽', 'RUB']" :flag="flagUrl.russia" :balance="currentUser.balance" :account="currentUser.accounts.rub" @click.native="currentModal='accountRequisites', thisCurrency='rub'" />
                
                </div>
                <loader class="loader" v-else></loader>
                <div class="panel button_desktop">
                    <button @click="currentModal='makeExtOutPay'">Make outgoing payment</button>
                    <button @click="currentModal='makeIntPay'">Make internal payment</button>
                    <button @click="currentModal='makeCurrentExchange'">Make current exchange</button>
                </div>
            </section>
            <footer>
                <div class="copyright"><span>© 2020 Copyright. All Rights Reserved. Internet banking software provided by Sterling Payment Services (HK)</span>
                </div>
            </footer>
            <component :is="currentModal" @close="currentModal=''"  :transactions="allTransactions" :user="currentUser" :currency="getCurrency"  :thisCurrency="thisCurrency" @pushmsg="showpopup, currentModal=''" />
            <popUp v-if="msg" :msg="msg" @close="msg=''" />
        </div>
        
    </div>
</template>

<script>
    import loader from "../components/loader"
    import popUp from '../components/modals/popUp'

    import balance from "../components/balance.vue"

    import transactionHistory from "../components/modals/transactionHistory"
    import makeExtOutPay from "../components/modals/makeExtOutPay"
    import makeIntPay from "../components/modals/makeIntPay"
    import makeCurrentExchange from "../components/modals/makeCurrentExchange"

    import profile from "../components/modals/profile"
    import info from "../components/modals/info"
    import accountRequisites from "../components/modals/accountRequisites"
    import {mapGetters, mapActions} from 'vuex'
    
    export default{
        name: "desktop",
        components: {
            loader,
            popUp,
            balance,
            transactionHistory,
            makeExtOutPay,
            makeIntPay,
            makeCurrentExchange,
            profile,
            info,
            accountRequisites
        },
        data(){
            return {
                msg: "",
                loading: true,
                currentModal: "",
                thisCurrency: "",
                flagUrl: {
                    russia: require("@/assets/flags/russia.png"),
                    usa:  require("@/assets/flags/usa.png"),
                    eu:  require("@/assets/flags/eu.png"),
                    gb:  require("@/assets/flags/gb.png"),
                }
            }
        },
         methods: {
            showpopup(value){
                this.msg = value
                
            },
            formatId(){ 
                let id = JSON.parse(localStorage['user']).id
                let a = id.toString()
                let b = ""
                for(let i=0; i<4-a.length; i++){
                    b+="0"
                }
                let position = 0
                let output = [a.slice(0, position), b, a.slice(position)].join('')
                return output     
            },
            getInfoFromLocal(){
                let user = JSON.parse(localStorage['user'])
                return user
            },
            ...mapActions(['fetchUsers', 'fetchCurrency', 'fetchCurrentUser', 'fetchTransactions']),
            logOut(){
                localStorage.clear()
                this.$router.push('/login')
            }
        },
        computed: {
            ...mapGetters(['allUsers', 'getCurrency', 'currentUser', 'allTransactions']),
        },
        async mounted(){
            await this.fetchCurrency().then(()=>{
            }).catch(error => {
                this.err = error
                console.log(error)
            })

            await this.fetchTransactions().then(()=>{
                console.log('transactions ')          
            }).catch(error => {
                this.err = error
                console.log(error)
            })

            await this.fetchCurrentUser(this.getInfoFromLocal().id).then(()=>{
                this.loading = false
                this.user = this.allUsers[0]
            }).catch(error => {
                this.err = error
                console.log(error)
            })

            await this.fetchUsers().then(()=>{
                
                this.user = this.allUsers[0]
            }).catch(error => {
                
                this.err = error
                console.log(error)
            })
                
    
        }
    }
</script>


