<script>
    import currencySelector from "../forms/currencySelector"
    export default{
        name: "extOutPay",
        components:{
            currencySelector
        },
        props: {
            user: Object,
            transactions: Array
        },
        data: function(){ 
            return {
                selected: 'eur',
                
                transaction: { 
                    type: "external",
                    status: "pending",
                    payer: {
                        clientName: this.user.name.firstname + " " + this.user.name.lastname,
                        clientAdress: this.user.company.address
                    },
                    transactionDetails: {
                        id: this.newID(),
                        bankAccount: null,
                        currency: null,
                        amount: null,
                        valueDate: new Date() // date format
                    },
                    beneficiaryBank: {
                        swift: "",
                        name: "",
                        address: "",
                        country: ""
                        
                    },
                    beneficiaryClient: {
                        type: "",
                        name: "",
                        address: "",
                        country: "",
                        iban: ""
                    },
                    paymentDescription: {
                        referenceMsg: "",
                        msg2: "",
                        msg3: "",
                        msg4: ""
                    }
                },
                
            }
        },
        filters:{
            formatAccount(account){
                account = "0" + account.toString()
            
               
                let output = account.substr(0, 3)
                output += " " + account.substr(3, 2)
                output += " " + account.substr(5, 3)
                output += " " + account.substr(8, 4)
               
                return output
            },
            formatTransactionID(value){ 
                
                let a = value.toString()
                let b = ""
                for(let i=0; i<19-a.length; i++){
                    b+="0"
                }
                let position = 0
                let output = [a.slice(0, position), b, a.slice(position)].join('')
                return output     
            },
            formatDate(value){

                if (!value) return ''
                value = value.toString()
                value = value.slice(3, 16).replace(/-/g, ".")
                return value
                
            }
        },
        methods: {
            checkValue(type, amount, available){
                    if ( amount > available ) {
                   
                      this.transaction.transactionDetails.amount = this.user.balance.total[this.selected] - this.user.balance.blocked[this.selected]
                    }
                },

           
            async makePayment(){
                let newPayment = this.transaction
                newPayment.transactionDetails.currency = this.selectedCurrency
                newPayment.transactionDetails.bankAccount = this.getAccount
               
                
                this.$emit('created')
                
                   let response = await fetch("http://localhost:3000/makepayment", {
                    method: 'POST',
                    headers: {
                        'Content-Type': 'application/json;charset=utf-8'
                        },
                    body: JSON.stringify(newPayment)
                    }).then(
                       
                    )
             
                
                    
                },
                newID(){
                    if (this.transactions.length !==0){
                        let id = this.transactions[this.transactions.length - 1].transactionDetails.id
                        return id + 1
                    } else {
                        return 1
                    }
                    
                },
                close(){
                    
                }
                
        },
        watch:{
            selected(){
                this.transaction.transactionDetails.amount = null
            }
        },
        computed: {
            
            getBalance(){
                let currency = this.selected
                
                let balance = {
                    total: this.user.balance.total[currency].toFixed(2),
                    available: this.user.balance.total[currency] - this.user.balance.blocked[currency].toFixed(2),
                    blocked: this.user.balance.blocked[currency].toFixed(2)
                }
                return balance
            },
        
            selectedCurrency(){
                return this.selected
            },
            getAccount(){
                const accounts = {
                    "eur" : 99790000001,
                    "gbp" : 99280000001,
                    'usd' : 99480000001,
                    "rub" : 99460000001
                    }
                let account = accounts[this.selected]
                this.$emit('recieveAccount', account)
                return account
          },
          getCurrencySign(){
              let currency = this.selected
                const signs = {
                    "eur" : "€",
                    "gbp" : "£",
                    'usd' : "$",
                    "rub" : "₽"
                    }
                let sign = signs[currency]
                return sign
          }
        }
    
    }
</script>
