<template>
    <div class="container">
        <div class="main">
            <div class="sell__container">
                <div class="sell">
                    <h3>Sell</h3>
                    <div class="sell_items">
                        <button @click="sellSelected='usd'" :class="{active: sellSelected == 'usd'}" :disabled="buySelected=='usd'">usd</button>
                        <button @click="sellSelected='eur'" :class="{active: sellSelected == 'eur'}" :disabled="buySelected=='eur'">eur</button>
                        <button @click="sellSelected='gbp'" :class="{active: sellSelected == 'gbp'}" :disabled="buySelected=='gbp'">gbp</button>
                        <button @click="sellSelected='rub'" :class="{active: sellSelected == 'rub'}" :disabled="buySelected=='rub'">rub</button>
                    </div>
                </div>
                <div class="sell__amount">
                    <h3>Amount</h3>
                    
                    <div class="sell__amount_items">
                        <!-- <span>1200.25</span> -->
                        <input type="text" @keyup="checkValue('sell', sellAmount, getBalance.sell.available)" v-model="sellAmount" >
                        <div class="sell__amount_total">
                            <p>total: <span>{{getBalance.sell.total}}</span> {{getCurrencySign.sell}}</p>
                            <p>available: <span>{{getBalance.sell.available}}</span> {{getCurrencySign.sell}}</p>
                            <p>blocked: <span>{{getBalance.sell.blocked}}</span> {{getCurrencySign.sell}}</p>
                        </div>
                    </div>
                </div>
            </div>

            <div class="separator">
            </div>

            <div class="buy__container">
                <div class="buy">
                    <h3>Buy</h3>
                    <div class="buy_items">
                        <button @click="buySelected='usd'" :class="{active: buySelected == 'usd'}" :disabled="sellSelected=='usd'">usd</button>
                        <button @click="buySelected='eur'" :class="{active: buySelected == 'eur'}" :disabled="sellSelected=='eur'">eur</button>
                        <button @click="buySelected='gbp'" :class="{active: buySelected == 'gbp'}" :disabled="sellSelected=='gbp'">gbp</button>
                        <button @click="buySelected='rub'" :class="{active: buySelected == 'rub'}" :disabled="sellSelected=='rub'">rub</button>
                    </div>
                </div>
                <div class="buy__amount">
                    <h3>Amount</h3>
                    <div class="buy__amount_items">
                        <!-- <span>1200.25</span> -->
                        <input type="text" @keyup="checkValue('buy', buyAmount, getBalance.buy.available)" v-model="buyAmount" :disabled="true">
                        <div class="buy__amount_total">
                            <p>total: <span>{{getBalance.buy.total}}</span> {{getCurrencySign.buy}}</p>
                            <p>available: <span>{{getBalance.buy.available}}</span> {{getCurrencySign.buy}}</p>
                            <p>blocked: <span>{{getBalance.buy.blocked}}</span> {{getCurrencySign.buy}}</p>
                        </div>
                    </div>
                </div>
                <div class="buy__rate">
                    <h3>Rate</h3>
                    <div class="buy__rate_items">
                        <span>{{getRate || 1}}</span>
                    </div>
                </div>
            </div>
        </div>

        <div class="confirm">
            
            <input type="text" placeholder="enter google authenticator code">
            <button @click="makeExchange()">SEND</button>
        </div>
    </div>

</template>
<script>
   

    export default {
        name: "currentExchange",
        data: function(){ 
            return {
                selected: 'eur',
                selectedAccount: this.getAccount,
                sellAmount: null,
                buyAmount: null,
                buySelected: 'eur',
                sellSelected: 'usd',
                transaction: { 
                    type: "current exchange",
                    status: "pending",
                    payer: {
                        clientName: this.user.name.firstname + " " + this.user.name.lastname,
                        clientAdress: this.user.company.address
                    },
                    transactionDetails: {
                        id: this.newID(),
                        bankAccount: null,
                        currency: null,
                        amount: null,
                        valueDate: new Date() 
                    },
                    beneficiaryBank: {
                        swift: "",
                        name: "Sberbank",
                        address: "Moscow",
                        country: "Russia"
                        
                    },
                    beneficiaryClient: {
                        type: "none",
                        name: "Vasya",
                        address: "London",
                        country: "Great Britain",
                        iban: "GB29 NWBK 6016 1331 9268 19"
                    },
                    paymentDescription: {
                        referenceMsg: "",
                        msg2: "",
                        msg3: "",
                        msg4: ""
                    }
                },
                
            }
        },
        props: {
            currency: Object,
            user: Object,
            transactions: Array
        },
        methods: {
            async makeExchange(){
                let exchange = {
                    id: this.user.id,
                    balance: {
                        total: this.user.balance.total,
                        blocked: this.user.balance.blocked
                    }
                    
                }
                exchange.balance.total[this.sellSelected] -= this.sellAmount / this.getRate
                exchange.balance.total[this.buySelected] += this.buyAmount * this.getRate
                console.log(exchange)
                let response = await fetch("http://localhost:3000/currencyexchange", {
                    method: 'POST',
                    headers: {
                        'Content-Type': 'application/json;charset=utf-8'
                        },
                    body: JSON.stringify(exchange)
                    }).then( () => console.log('ok'))
                    
                },
                newID(){
                    if (this.transactions.length !==0){
                        return this.transactions.pop().transactionDetails.id + 1
                    } else {
                        return 1
                    }
                    
                },
                checkValue(type, amount, available){
                    
                    if (amount){
                        if (type=='sell') {
                            this.buyAmount = this.sellAmount / this.getRate
                            this.buyAmount.toFixed(2)
                            }
                            else if (type=='buy') {
                                this.sellAmount = this.buyAmount * this.getRate
                                this.sellAmount.toFixed(2)
                            }
                            }

                          if ( amount > available ) {
                        if (type=='sell') {
                                this.sellAmount = this.getBalance.sell.available
                                this.sellAmount.toFixed(2)
                                }
                          if (type=='buy') {
                                this.buyAmount = this.getBalance.buy.available
                                this.buyAmount.toFixed(2)
                               }
                        }
                    
                }
               
                
        },
        
        mounted(){
            this.$emit('recieveAccount', this.user.accounts[this.sellSelected])
        },
        computed: {
            getBalance(){
                
                
                let balance = {
                    buy: {
                        total: this.user.balance.total[this.buySelected].toFixed(2),
                        available: this.user.balance.total[this.buySelected] - this.user.balance.blocked[this.buySelected].toFixed(2),
                        blocked: this.user.balance.blocked[this.buySelected].toFixed(2)
                    },
                    sell: {
                        total: this.user.balance.total[this.sellSelected].toFixed(2),
                        available: this.user.balance.total[this.sellSelected] - this.user.balance.blocked[this.sellSelected].toFixed(2),
                        blocked: this.user.balance.blocked[this.sellSelected].toFixed(2)
                    }
                }
                return balance
            },
        
            selectedCurrency(){
                return this.selected
            },
            getAccount(){
                
                let selected = this.selected
                
                if(selected){
                     const accounts = {
                    "eur" : this.user.accounts.eur,
                    "gbp" : this.user.accounts.gbp,
                    'usd' : this.user.accounts.usd,
                    "rub" : this.user.accounts.rub
                    }
                let account = accounts[selected]
                
                return account
                }
               
          },
          getCurrencySign(){
                const signs = {
                    "eur" : "€",
                    "gbp" : "£",
                    'usd' : "$",
                    "rub" : "₽"
                    }
                let currencySign = {
                    buy: signs[this.buySelected],
                    sell: signs[this.sellSelected]}
                return currencySign
          },
          getRate(){
              return this.currency[this.sellSelected][this.buySelected]
          }
        },
        watch: {
           
            sellSelected(){
                this.sellAmount = null
                this.$emit('recieveAccount', this.user.accounts[this.sellSelected])
                },
            buySelected(){
                this.buyAmount = null
            }
        }
    }
</script>
