<template>
    <div>
        <transition name="modal">
            <div class="modal-mask">
                <div class="modal-wrapper">
                    <div class="modal-container">
                        <div class="modal-body">
                            <header>
                                <div class="left">
                                    <img src="../../assets/logo_green_small.png" alt="">
                                    <div class="titles">
                                        <span class="history">Make external outgoing payment</span>
                                        <span class="bastion">Bank account № <span class="blue">{{account | formatAccount}}</span></span>
                                    </div>
                                </div>
                                <div class="right">
                                    <button class="close" @click="$emit('close')">&#9587;</button>
                                </div>
                            </header>
                            <div class="form-container">
                              
                                <extOutPay 
                                :user="user" 
                                :transactions="transactions" 
                                @recieveAccount="bankAccount" 
                                @close="$emit('close')" @created="pushmsg"/>
                            </div>

                        </div>
                    </div>
                </div>
            </div>
        </transition>

    </div>
</template>

<script>
    import extOutPay from "../forms/extOutPay"

    export default {
        name: "makeExtOutPay",
        components: {
            extOutPay
        },
        data: function () {
            return {
               account: ""
            }
        },
        props: {
            user: Object,
            transactions: Array,
            currency: Object
        },
        methods: {
            pushmsg(){
                this.$emit('pushmsg', 'transaction created!')
            },
            bankAccount(value){
               this.account =  value
            },
            close() {
                this.$emit('close')
            }
        },
        filters: {
            formatAccount(account){
                account = "0" + account.toString()
            
               
                let output = account.substr(0, 3)
                output += " " + account.substr(3, 2)
                output += " " + account.substr(5, 3)
                output += " " + account.substr(8, 4)
               
                return output
            },
        }
    }
</script>

