<template>
<div>
    <transition name="modal">
        <div class="modal-mask">
          <div class="modal-wrapper">
            <div class="modal-container">
              <div class="modal-body">
                  <header>
                    <div class="left">
                        <img src="../../assets/logo_green.png" alt="" width="120" height="120">
                        <div class="titles">
                            <span class="history">Make internal payment</span>
                            <span class="bastion">Bank account № <span class="blue">{{account | formatAccount}}</span></span>
                        </div>
                    </div>
                    <div class="right">
                        <button class="close" @click="$emit('close')">&#9587;</button>
                    </div>
                    </header>
                    <div class="form-container">
                        <intPay 
                            :user="user" 
                            :currency="currency" 
                            @recieveAccount="bankAccount" 
                            :transactions="transactions"
                            @close="$emit('close')"/>
                    </div>
              </div>
            </div>
          </div>
        </div>
      </transition>

    </div>
</template>

<script>
    import intPay from "../forms/intPay"
    export default{
        name: "historyModal",
        components: {
          intPay
        },
        data: function () {
            return {
                account: ""
            }
        },
        props: {
            currency: Object,
            user: Object,
            transactions: Array
        },
        methods: {
            bankAccount(value){
               this.account =  value
            },
            close() {
                this.$emit('close')
            }
        },
        filters: {
            formatAccount(account){
                account = "0" + account.toString()
            
               
                let output = account.substr(0, 3)
                output += " " + account.substr(3, 2)
                output += " " + account.substr(5, 3)
                output += " " + account.substr(8, 4)
               
                return output
            },
        }
    }
</script>

